body{
  background-color: #22254b;
  
}

#root{
  display: flex;
  flex-direction: column;
}

.movie {
  margin: 0.5rem;
  background-color: #373b69;
  border-radius: 3px;
  overflow: hidden;
  width: 250px;
  position: relative;
  box-shadow: 0px -3px 5px rgba(0, 0, 0, 0.1);
}

.movie img{
  max-width: 200%;
  width: 250px;
}

.movie span{
  font-size: 110%;
}

.movie-container {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  
}

.movie-info{
  display: flex;
  padding: 0.2rem;
  align-items: center;
  justify-content: space-between;
}

.movie-overview{
  position: absolute;
  background-color: aliceblue;
  bottom: 0;
  padding: 0.7rem;
  left: 0;
  right: 0;
  transform: translateY(102%);
  transition: transform 0.3s ease-in-out;
  overflow: auto;
  max-height: 100%;

}

.movie:hover .movie-overview{
  transform: translateX(0);
}

header{
  padding: 0.5rem;
  justify-content: center;
  display: flex;
}

.search {
  font-size: 1rem;
  border: 2px solid #7f83bd;;
  font-family: 'Noto Sans Mono', monospace;
  border-radius: 50px;
  padding: 0.5rem 1.2rem;
  color: tomato;
  background-color: transparent;
}

.form {
  justify-content: center;
  display: flex;
}

.add-movie{
  font-size: 1rem;
  border: 2px solid #7f83bd;;
  font-family: 'Noto Sans Mono', monospace;
  border-radius: 50px;
  padding: 0.5rem 1.4rem;
  color: tomato;
  background-color: transparent;
  justify-content: space-between;
  
}

.button{
display:inline-block;
padding:0.3em 1.2em;
margin:0 0.3em 0.3em 0;
border-radius:2em;
box-sizing: border-box;
text-decoration:none;
font-family:'Roboto',sans-serif;
font-weight:300;
color:#FFFFFF;
background-color:#4eb5f1;
text-align:center;
transition: all 0.2s;
}
a.button3:hover{
background-color:#4095c6;

@media all and (max-width:30em){
  a.button3{
  display:block;
 margin:0.2em auto;
  }
  }
}


